export function fromDto(value, id) {
    return {
        name: value.name,
        year: value.year,
        author: value.author,
        rating: value.rating,
        imageLink: value.imageLink,
        isbn: value.isbn,
        id: id,
    };
}
export function toDto(value) {
    return {
        name: value.name,
        year: value.year,
        author: value.author,
        rating: value.rating,
        imageLink: value.imageLink,
        isbn: value.isbn,
    };
}
