import { deleteBook, getSortDocs } from "../../firebase/firebase";
import { sort, getOrderBySorting, currentSortType } from "../sorting/sorting";
import { booksListContainer, renderBooks } from "../booksList";
export async function addDeleteEventClick() {
    let deleteButtons = document.querySelectorAll(".delete-book-button");
    deleteButtons.forEach((deleteButton) => deleteButton.addEventListener("click", (e) => handleClick(e)));
}
async function handleClick(e) {
    await deleteBook(e.target.parentElement.dataset.id);
    booksListContainer.innerHTML = "";
    await getSortDocs(currentSortType, getOrderBySorting(currentSortType)).then((allBooks) => {
        let catalog = allBooks;
        renderBooks(sort(catalog, currentSortType));
    });
}
